body {
    background-color: white;
    margin: 3rem auto;
    max-width: 250rem;
}

h1 {
    text-align: center;
    margin-bottom: 3rem;
}

section {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 5rem;
}
@media screen and (min-width: 35rem) {
    section {
        -ms-flex-direction: row;
            flex-direction: row;
    }
}
section > * {
    -ms-flex: 1 1;
        flex: 1 1;
}

article {
    padding: 0 1rem;
}
article p {
    font-family: "Georgia", sans-serif;
    font-size: 2rem;
    line-height: 1.8;
}
@media screen and (min-width: 35rem) {
    article p {
        margin: 0;
    }
}
@media screen and (min-width: 35rem) {
    article p:not(:last-of-type) {
        min-height: 90vh;
    }
}

figure {
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: start;
        align-self: flex-start;
    background-color: white;
    display: block;
    height: 30rem;
    margin: 0;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    top: 3rem;
}
@media screen and (min-width: 35rem) {
    figure {
        position: -webkit-sticky;
        position: sticky;
    }
}

section {
    -ms-flex-line-pack:center;
        align-content:center;
    margin: 3rem 3rem;
    width: auto;
}

@media only screen and (max-device-width : 640px) {
    /* Styles */
    .App {
        width: 50rem;
    }

    body {
        width: 50rem;
    }
}

@media only screen and (max-device-width: 768px) {
    /* Styles */
}

.home-images {
    width: 35rem;
    height: 25rem;
    border: 0.7px black solid;
    margin: 30px 0px;
}

.hexagon {
    stroke: #000;
    stroke-width: 0.5px;
}

.startLetter {
    font-size: 24px;
}

.photo-image {
    height: 25rem;
    width : 25rem;
}

.navbar-default .navbar-nav>li>a {
    font-family: "Georgia", sans-serif;
    font-size: 1.6rem;
    line-height: 1.8;
}


